[class*='icon'] {
  display: inline-block;
  width: 24px;
  height: 24px;
  flex-shrink: 0;
  background-color: #666666;
  mask-size: cover;
  mask-repeat: no-repeat !important;
}

.icon--photos {
  mask: url("../images/icons/photo.svg");
}

.icon--selfies {
  mask: url("../images/icons/selfies.svg");
}

.icon--recent {
  mask: url("../images/icons/recent.svg");
}

.icon--delete {
  mask: url("../images/icons/delete.svg");
}

.icon--search {
  mask: url("/images/icons/search.svg");
}

body.body {
  font-family: "Commissioner", sans-serif;
  font-size: 16px;
  color: #666666;
}

.fw-500 {
  font-weight: 500;
}

.fw-700 {
  font-weight: 700;
}

.body a {
  text-decoration: none;
  color: #292929;
}

.body a:hover, .body a:active {
  color: #666666;
}

.body .collapsing {
  -webkit-transition: height 0s ease;
  -o-transition: height 0s ease;
  transition: height 0s ease;
}

@media (max-width: 991px) {
  .body .container {
    max-width: 100%;
  }
}

@media (max-width: 991px) {
  .menu-main.navbar {
    position: static;
  }
  .menu-main .navbar-toggler {
    padding: 0;
    border: none;
    width: 25px;
    height: 25px;
    position: relative;
  }
  .menu-main .navbar-toggler:focus {
    box-shadow: none;
  }
  .menu-main .navbar-toggler span {
    display: inline-block;
    height: 2px;
    width: 20px;
    position: absolute;
    transition: all .2s linear;
    background-color: #666666;
    right: 0;
  }
  .menu-main .navbar-toggler span.line-top {
    top: 5px;
  }
  .menu-main .navbar-toggler span.line-middle {
    top: 11px;
  }
  .menu-main .navbar-toggler span.line-bottom {
    top: 17px;
  }
  .menu-main .navbar-toggler[aria-expanded="true"] span.line-top {
    top: 11px;
    transform: rotate(45deg);
  }
  .menu-main .navbar-toggler[aria-expanded="true"] span.line-middle {
    opacity: 0;
  }
  .menu-main .navbar-toggler[aria-expanded="true"] span.line-bottom {
    transform: rotate(-45deg);
    top: 11px;
  }
  .menu-main .navbar-collapse {
    position: absolute;
    top: 74px;
    width: 100%;
    right: 0;
    background-color: white;
    z-index: 100;
    padding: 15px;
    max-width: 320px;
    min-height: calc(100vh - 74px);
    box-shadow: 0 6px 40px rgba(0, 0, 0, 0.25);
  }
  .menu-main .navbar-nav li a {
    display: block;
    padding: 5px 0;
  }
  .menu-main .navbar-nav ul a {
    display: flex;
    font-size: 0.8125rem;
    align-items: center;
    padding-left: 10px;
  }
  .menu-main .navbar-nav ul a i {
    margin-right: 11px;
  }
  .main-menu-opened::after {
    -webkit-transition: background-color .7s ease;
    transition: background-color .7s ease;
    content: "";
    display: block;
    position: absolute;
    z-index: 1000;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    top: 0;
    left: 0;
  }
}

.body .dropdown-toggle:after {
  border: none;
  background-image: url("../images/icons/chevron-down-small.svg");
  background-repeat: no-repeat;
  width: 13px;
  height: 8px;
  background-size: 13px auto;
  vertical-align: .16em;
}

@media (min-width: 992px) {
  .menu-main li {
    margin-right: 24px;
    line-height: 1.5rem;
  }
  .menu-main li .active {
    position: relative;
    font-weight: 700;
    color: #292929;
  }
  .menu-main li .active:after {
    content: '';
    display: block;
    height: 2px;
    position: absolute;
    background-color: #ED7135;
    width: 100%;
    bottom: -27px;
  }
}

.header {
  border-bottom: 1px solid #EBEBEB;
  padding: 16px 0;
  z-index: 1001;
  position: relative;
  background-color: white;
}

.header a {
  color: #666666;
}

.header-search {
  background-color: #EEEEEE;
  display: flex;
  border-radius: 100px;
  padding: 6px 8px 2px;
  align-items: center;
}

.header-search input {
  border: none;
  box-shadow: none;
  outline: none;
  transition: width .4s ease-in-out;
  padding: 0 0 7px;
  background-color: transparent;
  font-size: 1rem;
  width: 100%;
}

@media (max-width: 992px) {
  .header-search input {
    opacity: 0;
    width: 0;
  }
}

@media (max-width: 991px) {
  .header-search:hover input, .header-search.is-visible input {
    opacity: 1;
    width: 200px;
    padding: 0 12px 7px 12px;
  }
}

@media (min-width: 992px) {
  .header-search input {
    padding-left: 12px;
  }
}

.sidebar-left {
  padding-top: 56px;
  min-width: 273px;
}

.sidebar-menu a {
  font-weight: bold;
  line-height: 1.5rem;
  display: flex;
  align-items: center;
  height: 40px;
  width: 273px;
  padding: 10px 15px 12px 15px;
  color: #666666;
}

.sidebar-menu a i {
  margin-right: 11px;
}

.sidebar-menu .active a {
  background-color: #ED7135;
  color: white;
  border-radius: 8px;
}

.sidebar-menu .active a i {
  background-color: white;
}
